<template>
    <div class="log container">
        <form @submit.prevent="doneLog">
            <div class="form-floating mb-3">
                <input type="email" class="form-control" id="floatingInput" placeholder="name@example.com" v-model="user.email">
                <label for="floatingInput">Email address</label>
            </div>
            <div class="form-floating">
                <input type="password" class="form-control" id="floatingPassword" placeholder="Password" v-model="user.password">
                <label for="floatingPassword">Password</label>
            </div>
            <div class="alert alert-danger mt-3" v-if="msgErr">
                <a href="/#/Sign-up"> {{msgErr}} </a>
            </div>
            <div class="col-auto mt-4">
                <button type="submit" class="btn btn-primary mb-3">Confirm identity</button>
            </div>
        </form>
    </div>
</template>

<script>

// eslint-disable-next-line no-unused-vars
import firebase from '../firebase'
import router from '../router'
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";


export default {
    name: 'LogIn',
    data(){
        return{
            user:{
                email: '',
                password: '',
            },
            msgErr: ''
        }
    },
    methods:{
        doneLog(){
            const auth = getAuth();
            signInWithEmailAndPassword(auth, this.user.email, this.user.password)
            .then((uId)=>{
                localStorage.setItem("key", uId.user.uid);
                router.push({
                    name: 'home',
                })
            })
            .catch(()=>{
                    this.msgErr = 'Invalid Email Or Password'
                })
        }
    }
}
</script>