<template>
    <div class="section">
            <div class="container">
                <div class="row">
                    <div class="col-lg-6 img">
                    </div>
                    <div class="col-lg-6 information">
                        <div class="title">
                            <h1>creat account</h1>
                        </div>
                        <form @submit.prevent="donRegister">
                            <div class="name">
                                <fontAwesome icon='user' />
                                <input type="text" placeholder="your name" name="name" required v-model="user.name">
                            </div>
                            <div class="email">
                                <fontAwesome :icon="['far', 'envelope']" />
                                <input type="email" placeholder="email address" name="email" required v-model="user.email">
                            </div>
                            <div class="password">
                                <fontAwesome icon='lock' />
                                <input  :type="type" placeholder="password" name="password" required id="pass" v-model="user.password">
                                <!-- <input v-else type="password" placeholder="password" name="password" required id="pass" v-model="user.password"> -->
                                <fontAwesome icon='eye-slash' @click="toggleShow" />
                            </div>
                            <div class="confirm-pass">
                                <fontAwesome icon='lock' />
                                <input  :type="type" placeholder="confarm password" name="password" required id="cPass" v-model="user.cPassword">
                                <!-- <input v-else type="password" placeholder="confarm password" name="password" required id="cPass" v-model="user.cPassword"> -->
                                <fontAwesome icon='eye-slash' @click="toggleShow" />
                            </div>
                            <div class="phone">
                                <fontAwesome icon='phone' />
                                <input type="tel" placeholder="phone number" name="phone" required v-model="user.phone">
                            </div>
                            <div class="checkd">
                                <input type="checkbox" name="accept" id="accept" required>
                                <label for="accept"> i agree to the temis & condition</label>
                            </div>
                            <input id="submit" type="submit" value="creat account" class="btn btn-primary">
                            <span> have an account <a href="../views/LogIn.vue">click here</a> </span>
                        </form>
                    </div>
                </div>
            </div>
        </div>
</template>
<script>

// eslint-disable-next-line no-unused-vars
import firebase from '../firebase'
import router from '../router'
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";
// eslint-disable-next-line no-unused-vars
import { getFirestore,addDoc, collection} from "firebase/firestore";
const firestore = getFirestore();

  const ordersCollection = collection(firestore, "users")

export default {
    name: 'signUp',
    data(){
        return{
            user:{
                name: '',
                email: '',
                password: '',
                cPassword: '',
                phone: '',
                userId: '',
            },
            type: "password"
        }
    },
    methods:{
        donRegister(){
            const auth = getAuth();
            if(this.user.password === this.user.cPassword){
                createUserWithEmailAndPassword(auth, this.user.email, this.user.password)
            .then((uData)=>{
              this.newDoc = addDoc(ordersCollection, {
              name: this.user.name,
              email: this.user.email,
              phone: this.user.phone,
              userId: uData.user.uid,
              admin: false,
              totalNum: 0,
              logo: '',
              vip: false,
              })
               router.push({
                   name: 'upload',
                })
                localStorage.setItem("key", uData.user.uid)
        })
            .catch((e)=>{
                // this.msgErr = 'Invalid Email Or Password'
                    console.log(e);
                })
                }else{
                    console.log("Wrong Password");
                }
        },
        toggleShow(){
            this.type == "text" ? this.type = "password": this.type = "text"
        }
    }
}
</script>
<style scoped>
.img{
    overflow: hidden;
    background-image: url('../assets/img/sign-up.jpg');
    background-size: cover;
    background-position: center;
}
.section{
    min-height: 100vh;
    display: flex;
    align-items: center;
}
.section .container{
    min-height: 660px
}
.section .container h1{
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}
.information form{
    height: 500px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}
.information form div {
    width: 90%;
    height: auto;
    position: relative;
}
.information form div svg:first-child{
    position: absolute;
    font-size: 20px;
    top: 50%;
    left: 35px;
    transform: translateY(-50%);
}
.information form div svg:last-child{
    position: absolute;
    font-size: 20px;
    top: 50%;
    right: 35px;
    transform: translateY(-50%);
    cursor: pointer;
}
.information form input:not(input[type="checkbox"], input[type="submit"]) {
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    display: block;
    height: 45px;
    background-color: rgba(200, 200, 200, .4);
    border: none;
    padding: 12px;
    padding-left: 45px;
    border-radius: 5px;
}
.information form input:not(input[type="checkbox"], input[type="submit"]):focus{
    border: none;
    outline: none;
}
.information form input::placeholder{
    color: #000;
}
.information form input[type="checkbox"]{
    margin-right: 10px;
}
.information form .checkd {
    text-align: center;
    user-select: none;
}

.information form input[type="submit"] {
    width: 80%;
    height: 45px;
    font-size: 20px;
    font-weight: 600;
}
</style>