<template>
  <nav class="header">
      <div class="container">
        <div class="logo">
          <router-link :to="{name: 'profile'}" > <img :src="img" :alt="userInfo.name"> </router-link>
        </div>
          <div class="home">
        <router-link to="/"> <fontAwesome icon="house" /> </router-link>
        </div>
        <div class="arrow">
        <router-link to="/about"> <fontAwesome icon="arrow-trend-up"/> </router-link>
        </div>
        <div class="cash">
        <router-link to="/admin"> <fontAwesome icon="money-bills"/> </router-link>
        </div>
        <div class="pay">
        <router-link to="/hello"> <fontAwesome icon="credit-card"/> </router-link>
        </div>
        <div class="logout">
        <router-link to="/hello" @click="signout"> <fontAwesome icon="arrow-right-from-bracket"/> </router-link>
        </div>
        <div class="adminPanel" v-if="userInfo.admin == true">
        <router-link to="/panel" @click="signout"> <fontAwesome icon="arrow-right-from-bracket"/> </router-link>
        </div>
      </div>
  </nav>
  <!-- <p> <fontIcon :icon="[ 'far','user' ]" /> </p> -->
</template>
<script>
import {
getFirestore,
collection,
onSnapshot,
query,
} from "firebase/firestore";
import router from '../router'
const firestore = getFirestore();
import { getAuth, signOut, onAuthStateChanged } from "firebase/auth";
    import axios from 'axios';

const customerOrder = query(collection(firestore, 'users'));

const auth = getAuth();
let linkD = '';

export default {
    name: 'navBar',
     data(){
    return {
      user: null,
      userInfo: {},
      img: `https://firebasestorage.googleapis.com/v0/b/article-7c173.appspot.com/o/${localStorage.getItem("key")}?alt=media&token=${linkD}`,
    }
  },
  created(){
    onAuthStateChanged(auth,(userAuth)=>{
      console.log(userAuth);
      if (userAuth) {
        this.user = userAuth
      }else{
        this.user = null
      }
    })
    onSnapshot(customerOrder, (querySnapshot)=>{
      querySnapshot.forEach((doc)=>{
          console.log(doc.data().userId);
    if (localStorage.getItem("key") == doc.data().userId) {
    this.userInfo = doc.data()
    }
      }
      )
    })
            axios.get(`https://firebasestorage.googleapis.com/v0/b/article-7c173.appspot.com/o/${localStorage.getItem("key")}`)
            .then((res) => {
                linkD = res.downloadTokens
            }).catch( err =>{
                console.log(err);
            })
    },
    methods: {
      signout(){
        signOut(auth).then(() => {
                localStorage.removeItem("key");
                this.userInfo = null
          router.push({
            name: 'login'
          })
          }).catch((error) => {
            // An error happened.
            console.log(error);
      });
      }
    }
}
</script>

<style scoped>
nav {
  padding: 30px;
}
nav .container{
  display: flex;
  justify-content: space-between;
}
nav a {
  font-weight: bold;
  color: #2c3e50;
}

nav a.router-link-exact-active {
  color: #42b983;
}
img{
  width: 50px;
  height: 50px;
  border-radius: 50%;

}
