<template>
    <div class="container">
        <div>
            <label for="formFileLg" class="form-label">Choose Your Img</label>
            <input class="form-control form-control-lg" id="formFileLg" type="file">
        </div>
        <button class="btn btn-primary" @click="upload"> up </button>
    </div>
</template>

<script>
        import { getStorage, ref, uploadBytes } from "firebase/storage";
export default {
    methods:{
upload(){
    const file = document.getElementById("formFileLg").files[0]
    const storage = getStorage();
const storageRef = ref(storage, localStorage.getItem("key"));
uploadBytes(storageRef, file).then((snapshot) => {
    console.log('Uploaded a blob or file!');
    console.log(snapshot);
});
    },
}
    }
</script>
<style scoped>
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 300px;
}
.btn{
    width: 100px;
}
</style>
