<script>
import StateUser from "../components/StateUser.vue";
// eslint-disable-next-line no-unused-vars
import firebase from '../firebase'
// eslint-disable-next-line no-unused-vars
import router from '../router'
import {
getFirestore,
collection,
onSnapshot,
query,
} from "firebase/firestore";
const firestore = getFirestore();
const customerOrder = query(collection(firestore, 'users'));

export default {
    name: "adminUser",
    data() {
        return {
            user:{},
            users: [],
            qutsions: [],
        };
    },
    created(){
    onSnapshot(customerOrder, (querySnapshot)=>{
      querySnapshot.forEach((doc)=>{
        //   console.log(doc.data().userId);
    if (localStorage.getItem("key") == doc.data().userId) {
    this.user = doc.data()
    }
      }
      )
    })
    },
    methods: {
        changeIcon() {
            let allBoxes = document.querySelectorAll(".boxes div");
            allBoxes.forEach(box => {
                box.classList.remove("active");
                box.onclick = function () {
                    box.classList.add("active");
                };
            });
        }
    },
    components: { StateUser }
}
</script>


